// src/utils/api.js
import axios from 'axios';

// Create an axios instance with default config
const api = axios.create({
  baseURL: 'http://localhost:8080',
  headers: {
    'Content-Type': 'application/json',
  },
});

// Add a request interceptor to add the auth token to every request
api.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('accessToken');
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

// Add a response interceptor to handle token refresh
api.interceptors.response.use(
  (response) => {
    return response;
  },
  async (error) => {
    const originalRequest = error.config;

    // If the error is due to an expired token (401 Unauthorized) and we haven't tried to refresh yet
    if (error.response?.status === 401 && !originalRequest._retry) {
      originalRequest._retry = true;

      try {
        // Call the refresh token endpoint
        const response = await axios.post('http://localhost:8080/api/auth/refresh', {}, {
          headers: {
            Authorization: `Bearer ${localStorage.getItem('accessToken')}`,
          },
        });

        // If we get a new token, update it in localStorage
        if (response.data?.data?.accessToken) {
          localStorage.setItem('accessToken', response.data.data.accessToken);

          // Update the Authorization header for the original request
          originalRequest.headers.Authorization = `Bearer ${response.data.data.accessToken}`;

          // Retry the original request with the new token
          return api(originalRequest);
        }
      } catch (refreshError) {
        console.error('[DEBUG_LOG] Token refresh failed in API interceptor:', refreshError);
        // If refresh token fails, redirect to login
        if (typeof window !== 'undefined') {
          localStorage.removeItem('accessToken');
          console.log('[DEBUG_LOG] Redirecting to login from API interceptor');
          // Use window.location for a more reliable redirect
          window.location.href = '/admin/login';
        }
        return Promise.reject(refreshError);
      }
    }

    // If the error is a 403 Forbidden, it means the user doesn't have permission
    if (error.response?.status === 403) {
      console.error('Permission denied');
      // You could redirect to an access denied page or show a message
    }

    return Promise.reject(error);
  }
);

export default api;
