// src/utils/tokenRefresh.js
import api from './api';

// Token refresh interval in milliseconds (e.g., every 15 minutes)
const REFRESH_INTERVAL = 15 * 60 * 1000;

let refreshInterval = null;

/**
 * Start a timer to periodically refresh the access token
 */
export const startTokenRefreshTimer = () => {
  console.log("[DEBUG_LOG] Starting token refresh timer");

  // Clear any existing interval
  if (refreshInterval) {
    console.log("[DEBUG_LOG] Clearing existing refresh interval");
    clearInterval(refreshInterval);
  }

  // Set up a new interval
  refreshInterval = setInterval(async () => {
    console.log("[DEBUG_LOG] Token refresh interval triggered");
    try {
      // Only attempt to refresh if we have a token
      const currentToken = localStorage.getItem('accessToken');
      console.log("[DEBUG_LOG] Current token exists:", !!currentToken);

      if (currentToken) {
        console.log("[DEBUG_LOG] Attempting to refresh token");
        const response = await api.post('/api/auth/refresh');
        console.log("[DEBUG_LOG] Token refresh response received", response.status);

        // Update the token in localStorage if we get a new one
        if (response.data?.data?.accessToken) {
          console.log("[DEBUG_LOG] New token received, updating localStorage");
          localStorage.setItem('accessToken', response.data.data.accessToken);
          console.log('Access token refreshed successfully');
        } else {
          console.log("[DEBUG_LOG] No new token in response");
        }
      }
    } catch (error) {
      console.error('[DEBUG_LOG] Failed to refresh token:', error);

      // Only redirect to login for authentication errors (401/403)
      if (error.response && (error.response.status === 401 || error.response.status === 403)) {
        console.log("[DEBUG_LOG] Auth error during token refresh, redirecting to login");
        // If refresh fails due to auth error, stop the timer and redirect to login
        stopTokenRefreshTimer();
        if (typeof window !== 'undefined') {
          localStorage.removeItem('accessToken');
          console.log("[DEBUG_LOG] Using Next.js router compatible navigation");
          // Use window.location for a more reliable redirect
          window.location.href = '/admin/login';
        }
      } else {
        console.log("[DEBUG_LOG] Non-auth error during token refresh, continuing");
      }
      // For other errors, just log and continue - don't redirect
    }
  }, REFRESH_INTERVAL);

  return refreshInterval;
};

/**
 * Stop the token refresh timer
 */
export const stopTokenRefreshTimer = () => {
  if (refreshInterval) {
    clearInterval(refreshInterval);
    refreshInterval = null;
  }
};
