// src/app/admin/layout.jsx
"use client";

import { useEffect, useState } from "react";
import { useRouter, usePathname } from "next/navigation";
import Sidebar from "@/components/Sidebar";
import Header from "@/components/Header";
import "../../styles/globals.css";
import { startTokenRefreshTimer, stopTokenRefreshTimer } from "@/utils/tokenRefresh";

export default function AdminLayout({ children }) {
  const router = useRouter();
  const pathname = usePathname();
  const [isLoading, setIsLoading] = useState(true);
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  useEffect(() => {
    console.log("[DEBUG_LOG] AdminLayout effect running, pathname:", pathname);

    // Skip authentication check for login page
    if (pathname === "/admin/login") {
      console.log("[DEBUG_LOG] On login page, skipping auth check");
      setIsLoading(false);
      return;
    }

    // Check if user is authenticated
    const accessToken = localStorage.getItem("accessToken");
    console.log("[DEBUG_LOG] Access token exists:", !!accessToken);

    if (!accessToken) {
      console.log("[DEBUG_LOG] No access token, redirecting to login");
      setIsLoading(false); // Set loading to false before navigation
      router.push("/admin/login");
    } else {
      // Start the token refresh timer when user is authenticated
      console.log("[DEBUG_LOG] Starting token refresh timer");
      startTokenRefreshTimer();
      setIsLoading(false);
      setIsAuthenticated(true);
      console.log("[DEBUG_LOG] Authentication complete, ready to render");
    }

    // Clean up the timer when the component unmounts
    return () => {
      console.log("[DEBUG_LOG] Cleaning up token refresh timer");
      stopTokenRefreshTimer();
    };
  }, [pathname, router]); // Added router dependency back

  // Don't render layout for login page
  if (pathname === "/admin/login") {
    return <>{children}</>;
  }

  // Show loading state
  if (isLoading) {
    return <div className="flex items-center justify-center min-h-screen">Loading...</div>;
  }

  // Redirect to login if not authenticated and not on login page
  if (!isAuthenticated && pathname !== "/admin/login") {
    console.log("[DEBUG_LOG] Not authenticated, redirecting to login from layout");
    // Use window.location for a more reliable redirect
    if (typeof window !== 'undefined') {
      window.location.href = '/admin/login';
      return null;
    }
    return <div className="flex items-center justify-center min-h-screen">Redirecting to login...</div>;
  }

  // Only render the layout if authenticated or on login page
  return (
    <div className="flex min-h-screen">
      <Sidebar />
      <main className="flex-1">
        <Header />
        <div className="p-6">{children}</div>
      </main>
    </div>
  );
}
